.button-signup {
  border: none;
  background-color: $secondary-color-darker;
  color: $white;
  padding: 1.5rem 1rem;
  display: flex;
  justify-content: space-between;
  font-size: 18px;
  cursor: pointer;
  transition: 0.3s;
  width: 100%;

  &:hover {
    background-color: $secondary-color;
  }

  &__label {
    text-align: left;
    font-weight: 600;
    line-height: 1.28;
  }

  @include large {
    width: 100%;
    aspect-ratio: 1/1;
    flex-direction: column-reverse;

    .button-signup__icon {
      align-self: flex-end;
      margin-bottom: 1rem;
    }
  }
}
