.insights-listing {
  background-color: $background-color;

  @include up-to-large {
    padding-bottom: 0;
  }

  &__content {
    display: flex;
    flex-direction: column-reverse;

    @include large {
      flex-direction: row;
      justify-content: space-between;
    }
  }

  &__signup {
    margin-top: 4rem;
    margin-left: -1rem;
    margin-right: -1rem;

    @include medium {
      margin-left: -2rem;
      margin-right: -2rem;
    }

    @include large {
      margin: 0;
      width: calc(20% - 2rem);
    }

    @include xl {
      width: calc(20% - 3rem);
    }
  }

  &__items {
    @include large {
      width: 80%;
    }
  }
}
