.esg-listing {
  background-color: $white;

  .swiper-grid {
    width: 100%;
    @include large {
      margin-left: -1rem;
    }

    .swiper-container {
      width: 100%;

      .swiper-wrapper {
        @include xl {
          grid-template-columns: repeat(5, 1fr);
        }
      }
    }
  }
}
