@import 'accordion-js/dist/accordion.min.css';

.accordion {
  &__title {
    margin-bottom: 1rem;
  }
}

.accordion-item {
  margin: 0;
  border: none;
  border-bottom: 1px solid $border-color;

  .ac-trigger {
    padding: 1.5rem 0;
    color: inherit;
    font-size: inherit;
    font-family: inherit;
    outline: none;

    &[aria-expanded='true'],
    &:focus {
      color: inherit;
    }

    &:focus-visible {
      outline: auto;
    }

    @include large {
      padding: 2rem 0;
    }

    &::after {
      content: '+';
      width: 48px;
      height: 48px;
      background-color: $primary-color;
      border-radius: 50%;
      color: white;
      display: flex;
      justify-content: center;
      align-items: center;
      font-family: $font-body;
      font-size: 2rem;
      font-weight: 100;
      line-height: 1;
      transition: 0.3s;
    }
  }

  &.is-active > .ac-header .ac-trigger::after {
    content: '−';
    background-color: $secondary-color;
  }

  &__body {
    padding: 0 0 1.5rem 0;
    color: $primary-color;
  }
}
