.card {
  &.card-funds {
    aspect-ratio: unset;
    background-color: rgba(255, 255, 255, 0.9);

    .card__title {
      color: $primary-color;
    }

    .card__tags {
      @include tags-listing($color: $primary-color);
    }

    .card__intro {
      opacity: 1;
      visibility: visible;
      color: $text-dark;
    }
  }
}
