.card {
  position: relative;
  overflow: hidden;
  padding: 1rem 1rem 5rem 1rem;

  @include xl2 {
    padding: 2rem 2rem 6rem 2rem;
  }

  &__info {
    position: relative;
    z-index: 1;
  }

  &__title {
    line-height: 1.3;
    color: $white;
    transition: 0.3s;
    margin-bottom: 10px;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
    overflow: hidden;
    font-family: $font-heading;

    @include fluid-font(1.5rem, 0.8333);
  }

  &__intro {
    color: $primary-color;
    transition: 0.5s;
    opacity: 0;
    visibility: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }

  &__button {
    position: absolute;
    bottom: 1rem;
    right: 1rem;
    z-index: 1;

    @include xl3 {
      bottom: 2rem;
      right: 2rem;
    }
  }
}
