/* stylelint-disable */

$table-border-color: #f6f6f6;
$table-text-color: $text-dark;

$table-row-border: 1px solid #f6f6f6;

.scrollable {
  overflow-x: auto !important;
  scrollbar-width: thin;
  padding-bottom: 0.5rem;

  // firefox fix
  scrollbar-color: $primary-color #d9d9d9;

  &::-webkit-scrollbar-track {
    background-color: #d9d9d9;
  }

  &::-webkit-scrollbar {
    height: 6px;
  }

  &::-webkit-scrollbar-thumb {
    background-color: #595959;
    border-radius: 0;
  }
}

.table {
  border-radius: 0;
  border-spacing: 0;
  margin-bottom: 0.5 * $space;
  width: 100%;
  text-align: left;
  border-collapse: collapse;

  thead {
    background-color: #f6f6f6;
  }
}

.table__cell {
  padding: 16px 16px 16px 0;
  text-align: left;
  font-size: 18px;
  max-width: 250px;
  min-width: 150px;
  font-family: $font-body;
  line-height: normal;
  word-break: normal;

  @include medium {
    padding: 16px 32px 16px 0;
    max-width: 300px;
  }

  &:first-of-type {
    padding-left: 0.5rem;
  }
}

.table__heading-cell {
  padding: 16px 16px 16px 0;
  font-family: $font-body;
  font-weight: 600;
  font-size: 18px;
  letter-spacing: 0;
  line-height: normal;
  max-width: 300px;
  word-break: normal;
  color: $primary-color;

  @include medium {
    padding: 16px 32px 16px 0;
    min-width: 150px;
  }

  &:first-of-type {
    padding-left: 0.5rem;
  }
}

.table__row {
  border: 0;
  border-bottom: $table-row-border;
}

.table__footer-cell,
.table__footer-cell:first-child {
  border-bottom: none;
}

// TODO: BEM selectors should be applied to wysiwyg-created content

.text-long {
  table {
    @extend .table;
  }

  th {
    @extend .table__heading-cell;
  }

  td {
    @extend .table__cell;
  }

  tr {
    @extend .table__row;
  }

  tfoot th {
    @extend .table__footer-cell;
  }
}

.body-copy,
.accordion {
  .table-wrapper {
    overflow-x: auto;
    scrollbar-width: thin;
    padding-bottom: 1.5rem;
    // firefox fix
    scrollbar-color: $primary-color #d9d9d9;

    &::-webkit-scrollbar-track {
      background-color: #d9d9d9;
    }

    &::-webkit-scrollbar {
      height: 6px;
    }

    &::-webkit-scrollbar-thumb {
      background-color: #595959;
      border-radius: 0;
    }
  }

  table {
    border: none;
    min-width: 100%;
    border-collapse: collapse;
    margin-bottom: 0.5 * $space;
    overflow-x: auto;
    text-align: left;

    thead {
      text-align: left;
      color: $primary-color;
      background-color: #f6f6f6;
    }

    tbody {
      border: 0;
    }

    th,
    td {
      border: none;
      padding: 20px 40px 20px 0;
      border-bottom: $table-row-border;
      word-break: normal;
      vertical-align: top;
      position: relative;

      &:last-of-type {
        padding-right: 0;
      }
    }

    th {
      @include fluid-font(1.25rem, 0.8);
    }

    td {
      min-width: 280px;
      width: 280px;
      line-height: normal;
      font-size: 18px;

      @include fluid-font(1.125rem, 0.8);

      p:first-of-type {
        margin-top: 0;
      }
    }
  }
}

.table-heading {
  margin: 0 !important;
  font-weight: 600;
  font-size: 16px;

  @include medium {
    font-size: 18px;
  }
}

.table-footer {
  font-size: 14px;
  margin-top: 24px;
}

.table__caption {
  margin-top: 0.5rem;
  font-size: 14px;
}
