@mixin link {
  color: $primary-color;
  font-weight: 600;
  text-decoration: underline;
  text-decoration-color: $accent-color;
  text-decoration-thickness: 2px;
  text-underline-position: under;
  transition: 0.3s;

  &:hover {
    color: $accent-color;
  }
}

.link {
  @include link;
}

p {
  a {
    @include link;
  }
}
