ul {
  @include list-reset;

  list-style-type: disc;
}

ol {
  @include list-reset;

  list-style-type: decimal;
}

.body-copy,
.accordion-def,
.cke_editable {
  ul,
  ol {
    padding-left: 0;
  }

  ul {
    list-style: none;
    margin: 2 * $space 0;
  }

  li {
    margin-left: $space * 1.5;
    position: relative;
    margin-bottom: 8px;
  }

  ul li::before {
    content: '';
    background: $accent-color;
    width: 8px;
    height: 8px;
    position: absolute;
    top: 6px;
    left: -1.5rem;

    @include larger {
      top: 8px;
    }
  }

  ul.icon-bullet-list {
    li {
      padding-left: 10px;

      &::before {
        width: 20px;
        height: 20px;
        top: 2px;
        background-image: url('../images/icons/favicon.svg');
        background-color: transparent;
        background-position: center;
        background-size: contain;
      }
    }
  }

  ol {
    list-style: none;
    counter-reset: my-counter;
  }

  ol li {
    counter-increment: my-counter;
    padding-left: 0;
  }

  ol li::before {
    content: counter(my-counter) '. ';
    color: $grey-2;
    position: absolute;
    top: 0;
    left: -1.5rem;
  }

  small {
    ul,
    ol,
    ol li::before {
      color: $grey-2;
    }
  }
}

.list-item {
  margin-bottom: 0.2em;
  padding-left: 0;
}

// TODO: BEM selectors should be applied to wysiwyg-created content

.text-long {
  ol,
  ul {
    li {
      @extend .list-item;
    }
  }
}
