.resource-item {
  display: block;

  &__wrap {
    display: flex;
  }

  &__icon {
    width: 16px;
    margin-right: 1rem;
  }

  &__name {
    line-height: 1.4;
    transition: 0.1s;
  }

  &__blurb {
    margin-top: 0.5rem;
  }

  &.feature-resource {
    .resource-item__name {
      color: $primary-color;
    }

    &:hover {
      .resource-item__name {
        text-shadow: none;
      }
    }
  }

  &:hover {
    .resource-item__name {
      color: $primary-color;
      text-decoration: underline;
      text-underline-position: under;
      text-decoration-color: $accent-color;
      text-shadow: 0.8px 0 0 $primary-color;
    }
  }
}
