.body-copy,
.filter-accordion,
.accordion,
.cke_editable {
  .image-wrapper {
    overflow: hidden;

    &[data-align='right'] {
      @include medium {
        float: right;
        padding-left: $space;

        .image-caption {
          width: calc(100% - 16px);
          margin-left: $space;
        }
      }
    }

    &[data-align='left'] {
      @include medium {
        float: left;
        padding-right: $space;

        .image-caption {
          width: calc(100% - 16px);
          margin-right: $space;
        }
      }
    }

    &[data-align='center'] {
      @include medium {
        .image-caption {
          margin: auto;
        }
      }
    }
  }

  img {
    margin: auto;
    object-fit: cover;

    &[data-align='right'] {
      @include medium {
        float: right;
        padding-left: $space;
      }
    }

    &[data-align='left'] {
      @include medium {
        float: left;
        padding-right: $space;
      }
    }
  }

  .image-caption {
    @extend .h4;

    background-color: $secondary-color;
    padding: 1.5 * $space;
    margin: 0;
    margin-bottom: 2 * $space;
    clear: both;

    @include up-to-medium {
      margin: auto;
    }

    &[data-align='undefined'] {
      margin-left: auto;
      margin-right: auto;
    }
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    color: $primary-color;
  }

  p {
    &:not(:last-of-type) {
      margin-bottom: 1rem;
    }
  }
}

.body-copy__title {
  color: $primary-color;
}

.clear {
  clear: both;
}
