.disclaimer {
  padding-top: 2rem;
  border-top: 1px solid $border-color;

  &__content {
    font-size: 14px;

    a {
      color: $text-dark;
      font-weight: 600;
      text-decoration: underline;
      text-decoration-color: $text-dark;
      text-decoration-thickness: 1px;
      text-underline-position: under;
      transition: 0.3s;

      &:hover {
        color: $accent-color;
        text-decoration-color: $accent-color;
      }
    }
  }
}
