.basic-banner {
  .two-columns {
    @include xl {
      height: 391px;
    }
  }

  .two-columns__content {
    @include up-to-large {
      padding-bottom: 2.5rem;
    }
  }

  .two-columns__media {
    position: relative;
    overflow: hidden;
    display: none;

    @include large {
      display: block;
    }
  }

  .rectangle {
    position: absolute;
    bottom: -1rem;
    right: -32%;
    width: 110%;
    max-height: 95%;
    z-index: 1;

    @include xl {
      right: -20%;
    }

    @include xl5 {
      right: 0;
      width: 92.5%;
    }

    svg {
      width: 100%;
      height: 100%;
    }
  }

  &__media {
    width: 85%;
    margin-left: auto;
    aspect-ratio: 263/302;
    overflow: hidden;

    @include medium {
      aspect-ratio: 1/1;
    }

    @include large {
      width: 100%;
      height: 100%;
    }

    img {
      transform: scale(1.1);
    }

    img,
    video {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__breadcrumb {
    margin-bottom: 1.5rem;

    @include large {
      margin-bottom: 3.5rem;
    }

    .breadcrumb__item {
      font-size: 14px;
      color: $white;

      &:last-child {
        color: $white;
      }

      .breadcrumb__link {
        color: $white;
        font-size: inherit;
      }
    }
  }

  &__tags {
    @include tags-listing($color: #fff);
  }

  &__title {
    position: relative;

    @include fluid-font(3.375rem, 0.48);
  }
}
