.testimonial {
  &__container {
    padding: 2rem;
    position: relative;
    background-color: $white;
    overflow: hidden;

    @include large {
      padding-left: 4rem;
    }

    &.has-author-image {
      @include small-medium {
        padding-bottom: 0;
      }
    }

    &.grey {
      background-color: $background-color;
    }

    &.dark-green {
      background-color: $primary-color;
      color: white;
    }

    &.white {
      padding-bottom: 0;
    }
  }

  &__wrapper {
    max-width: 669px;
    position: relative;
    z-index: 1;
  }

  &__content {
    margin-bottom: 2rem;
  }

  &__text {
    font-style: italic;
    line-height: 1.44;
  }

  &__author {
    @include small-medium {
      display: flex;
      align-items: center;
    }
  }

  &__author-image {
    aspect-ratio: 1/1;
    width: 110px;
    margin-bottom: 1rem;

    @include small-medium {
      margin-bottom: 0;
      margin-right: 1rem;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__author-info {
    flex: 1;

    p {
      font-size: 16px;
    }
  }

  &__author-name {
    font-weight: $fw-600;
  }

  .squares {
    position: absolute;
    bottom: -10px;
    right: 0;
    z-index: 0;
    pointer-events: none;

    svg {
      width: 120px;
      height: 120px;

      @include medium {
        width: 160px;
        height: 160px;
      }

      @include large {
        width: 200px;
        height: 200px;
      }
    }
  }
}
