/*
 * General Mixins (put specific ones in component files where applicable)
*/

/* Mixin - Clearfix.
 * Adds clearfix based on http://bourbon.io/docs/#clearfix
 * use example =   @include cleafix
*/
@use "sass:math";

@mixin clearfix {
  &::after {
    clear: both;
    content: '';
    display: table;
  }
}

$outer-container-break: $small;

/// Mixin - Wrapper
/// Outer container mixin for large screens
@mixin wrapper(
  $container-max-width: $max-width,
  $outer-container-break: $small,
  $v-margin: 0,
  $v-padding: 0,
  $h-padding: $space,
  $h-padding-large: $h-padding
) {
  max-width: #{$container-max-width};
  width: 100%;
  margin: #{$v-margin} auto;
  padding: #{$v-padding} #{$h-padding};

  @include breakpoint($outer-container-break) {
    padding: #{$v-padding} #{$h-padding-large};
  }

  @include breakpoint($container-max-width) {
    padding-left: calc(
      #{$h-padding-large} + calc(-50vw + calc(#{$container-max-width} / 2))
    );
    padding-right: calc(
      #{$h-padding-large} + calc(-50vw + calc(#{$container-max-width} / 2))
    );
  }
}

// Mixin - Standard Margin
@mixin margin {
  margin-bottom: 1em;
}

@mixin no-bottom {
  margin-bottom: 0;
}

@mixin list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

 
// FUNCTION FOR THE FLUID FONT MIXIN
@function strip-unit($num) {
  @return math.div($num, $num * 0 + 1);
}

@function if-important($important) {
  @return #{if($important, "!important", "")};
}
  
  @mixin set-breakpoint($breakpoint) {
    @if $breakpoint == "" {
      @content;
    }
  
    @else {
      $value: map-get($breakpoints, $breakpoint);
  
      @if $value != null {
        @include breakpoint($value) {
          @content;
        }
      }
    }
  }
  
  // FLUID FONT
  $fluid-container-max-width: 120rem; // 1920px
  $fluid-container-min-width: 20rem; // 320px
  
  /* stylelint-disable */
  @mixin fluid-font($max-font-size, $fluid-font-scale: 0.5) {
    $min-font-size: $max-font-size * $fluid-font-scale;
  
    font-size: $max-font-size;
  
    @media screen and (max-width: $fluid-container-max-width) {
      font-size:
        calc(
          #{$min-font-size} + #{strip-unit($max-font-size - $min-font-size)} *
          (
            (100vw - #{$fluid-container-min-width}) / #{strip-unit(
              $fluid-container-max-width - $fluid-container-min-width
            )}
          )
        )
        ;
    }
  
    @media screen and (max-width: $fluid-container-min-width) {
      font-size: $min-font-size;
    }
  }

  @mixin tags-listing($color) {
    margin-bottom: 18px;
    display: flex;
    flex-wrap: wrap;

    .tag-item {
      font-size: 14px;
      text-transform: uppercase;
      display: flex;
      align-items: center;
      line-height: 1.5;
      color: $color;

      &:not(:first-of-type) {
        &::before {
          content: '';
          width: 4px;
          height: 4px;
          display: inline-block;
          background-color: $color;
          margin: 0 5px;
        }
      }
    }
  }