.card-contact {
  padding: 1rem;
  background-color: $background-color;

  @include large {
    padding: 2rem;
  }

  &__top {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }

  &__arrow {
    cursor: pointer;
    width: 40px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: flex-end;

    @include medium {
      display: none;
    }

    svg {
      transition: 0.3s;
    }

    &.active {
      svg {
        transform: rotate(-180deg);
      }
    }
  }

  &__title {
    margin-right: 1.5rem;
    color: $primary-color;
    line-height: 1.15;
  }

  &__content {
    max-height: 0;
    transition: 0.3s;
    overflow: hidden;
    opacity: 0;

    &.visible {
      max-height: 1000px;
      padding-top: 1rem;
      opacity: 1;
    }

    @include medium {
      padding-top: 1rem;
      max-height: unset;
      opacity: 1;
    }

    p {
      &:not(:last-of-type) {
        margin-bottom: 0.5rem;
      }
    }
  }
}
