.investment-manager-about {
  &__container {
    display: flex;
    flex-direction: column-reverse;

    @include medium {
      flex-direction: row;
    }

    @include xl3 {
      padding-right: 7rem;
    }
  }

  &__content {
    margin-top: 2rem;

    @include medium {
      margin-top: 0;
      width: 68%;
      padding-right: 2rem;
    }

    @include larger {
      width: 80%;
      padding-right: 3rem;
    }

    @include xl {
      padding-right: 5.625rem;
    }

    @include xl4 {
      padding-right: 8rem;
    }

    .body-copy {
      @extend .max-width-3;
    }
  }

  &__links {
    @include medium {
      width: 32%;
      margin-top: 4rem;
    }

    @include larger {
      width: 20%;
    }
  }
}
