.section-header {
  margin-bottom: 1.25rem;

  @include large {
    margin-bottom: 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
  }

  &.section_header-white {
    .section-header__title {
      color: $white;
    }
  }

  &__title {
    color: $primary-color;

    @include large {
      order: 1;
      width: 70%;
      margin: 0;
    }
  }

  &__intro {
    width: 100%;

    @include large {
      order: 3;
      margin-top: 1.25rem;

      p {
        max-width: 70%;
      }
    }
  }

  &__button {
    margin-top: 1.5rem;

    @include large {
      width: 30%;
      order: 2;
      margin: 0;
      display: flex;
      justify-content: flex-end;
    }
  }
}
