$big-margin: 0 0 2rem 0;
$small-margin: 0 0 $space;
$fluid-font-base: 4.5rem; // 72px

@mixin heading(
  $font-family: $font-heading,
  $font-size-max: $fluid-font-base,
  $font-size-scale: 0.666,
  $line-height: 1.1,
  $letter-spacing: 0,
  $font-weight: 300,
  $color: inherit,
  $color-link: inherit,
  $color-link-hover: inherit
) {
  color: #{$color};
  font-family: #{$font-family};
  font-weight: #{$font-weight};
  font-style: normal;
  line-height: #{$line-height};
  letter-spacing: #{$letter-spacing};
  margin-bottom: 1.25rem;
  width: auto;

  @include fluid-font($font-size-max, $font-size-scale);

  @include large {
    margin-bottom: 2rem;
  }

  &__link {
    color: $color-link;

    &:hover {
      color: $color-link-hover;
    }
  }
}

h1,
.h1 {
  @include heading($line-height: 1, $letter-spacing: 0);

  &.color {
    color: $primary-color;
  }
}

h2,
.h2 {
  @include heading(
    $font-size-max: $fluid-font-base * 0.5,
    $font-size-scale: 0.666,
    $line-height: 1.3,
    $letter-spacing: 0
  );

  &.color {
    color: $primary-color;
  }
}

h3,
.h3 {
  @include heading($font-size-max: $fluid-font-base * 0.333, $font-size-scale: 0.833, $line-height: 1);

  margin-bottom: 1rem;

  &.color {
    color: $primary-color;
  }
}

h4,
.h4 {
  font-family: $font-heading;
  font-size: 18px;
  margin-bottom: 1rem;

  &.color {
    color: $primary-color;
  }
}

.h5,
h5 {
  font-family: $font-heading;
  font-size: 16px;
}

h6,
.h6 {
  font-family: $font-heading;
  font-size: 14px;
}
