.campaign-materials {
  @include medium {
    position: relative;
    max-width: 1920px;
    margin-left: auto;
    margin-right: auto;

    &__image {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      width: 100%;
      height: 430px;
      overflow: hidden;
      z-index: 0;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }

  &__materials {
    margin-bottom: 2rem;
    z-index: 1;
    position: relative;
  }

  &__content {
    margin-top: 2rem;
    z-index: 1;
    position: relative;
  }

  &__text {
    @include medium {
      background-color: white;
      padding: 3rem 2rem;
    }

    @include xl {
      margin-left: -2rem;
      .body-copy {
        column-count: 2;
      }
    }
  }

  &__container {
    @include medium {
      display: flex;
      flex-direction: row-reverse;
      padding-top: 287px;
      max-width: 1500px;
      margin-left: auto;
      margin-right: auto;
    }
  }

  &__content {
    margin-top: 2rem;

    @include medium {
      margin-top: 0;
      width: 68%;
      padding-right: 20px;
    }

    @include larger {
      width: 70%;
    }

    @include xl4 {
      width: 73%;
    }

    .body-copy {
      @extend .max-width-3;
    }
  }

  &__materials {
    margin-top: -2rem;

    @include medium {
      margin-top: 0;
      width: 32%;
    }

    @include larger {
      width: 30%;
    }

    @include xl4 {
      width: 27%;
    }
  }
}
