.team-contact {
  &__container {
    padding: 1rem;
    max-width: 544px;
    background: $background-color;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 1rem;

    @media screen and (min-width: 500px) {
      grid-auto-rows: auto;
    }
  }

  &__image {
    aspect-ratio: 1/1;
    min-width: 110px;
    grid-column: 1/2;

    @media screen and (min-width: 500px) {
      grid-row: 1/5;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__info {
    grid-column: 2/4;

    @media screen and (min-width: 500px) {
      margin-top: 0.5rem;
    }
  }

  &__name {
    margin-bottom: 8px;
    line-height: 1.1;
  }

  &__phones,
  &__email {
    width: 100%;
    display: flex;
    grid-column: 1/4;
    margin-top: 1rem;

    @media screen and (min-width: 500px) {
      grid-column: 2/4;
    }

    .phone-icon,
    .email-icon {
      margin-right: 8px;
    }
  }

  &__email {
    .email-icon {
      margin-top: 3px;
    }

    a {
      font-weight: 600;
      color: $primary-color;
      transition: 0.3s;

      &:hover {
        text-decoration: underline;
        text-decoration-color: $accent-color;
        text-decoration-thickness: 2px;
        text-underline-position: under;
      }
    }
  }
}
