.card-esg {
  display: block;
  padding: 1rem;
  border: 1px solid transparent;
  transition: 0.3s;

  &:hover {
    border-color: $accent-color;

    .card-esg__title {
      color: $accent-color;
    }

    .card-esg__icon {
      img {
        filter: invert(78%) sepia(38%) saturate(416%) hue-rotate(112deg) brightness(88%) contrast(88%);
      }
    }
  }

  &__icon {
    margin-bottom: 1.5rem;

    img {
      max-width: 50px;
      max-height: 50px;
    }
  }

  &__title {
    color: $primary-color;
    margin-bottom: 1rem;
    transition: 0.3s;
  }
}
