.team-bio {
  &__top {
    @include small-medium {
      display: flex;
      align-items: center;
    }
  }

  &__image {
    aspect-ratio: 164/183;
    max-width: 164px;
    margin-bottom: 1rem;

    @include small-medium {
      aspect-ratio: 1/1;
      max-width: 290px;
      width: 40%;
      padding-right: 2rem;
      margin-bottom: 2rem;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__name {
    margin-bottom: 0;
  }

  &__selling-points {
    ul {
      margin-top: 1.5rem;
      margin-bottom: 1.5rem;
    }
  }
}
