.swiper-grid {
  width: 100%;

  .swiper-container {
    width: 100%;

    .swiper-wrapper {
      @include large {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        column-gap: 1rem;
        row-gap: 1rem;
      }
    }
  }

  .swiper-slide {
    width: 100%;
    height: auto;

    > div {
      height: 100% !important;
    }
  }
}
