.card {
  &.card-insight {
    aspect-ratio: 376/390;

    .card__image {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
      filter: grayscale(1);
    }

    .card__overlay {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(34, 98, 87, 0.5);
      z-index: 0;
      transition: 0.5s;
    }

    @media (hover: hover) {
      &:hover {
        .card__overlay {
          background-color: $white;
        }

        .card__title {
          color: $primary-color;
        }

        .card__intro {
          opacity: 1;
          visibility: visible;
        }

        .card__button {
          .button__arrow {
            svg {
              fill: $white;
            }

            background-color: $secondary-color;
          }
        }
      }
    }
  }
}
