.quick-links {
  @include large {
    max-width: 300px;
  }

  &__image {
    width: 100%;
    max-width: 300px;

    img {
      width: 100%;
    }
  }

  &__listing {
    display: flex;
    flex-direction: column;
  }

  &__link-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 0;

    &:not(:last-of-type) {
      border-bottom: 1px solid $border-color;
    }

    &-text {
      font-family: $font-body;
      font-weight: 600;
      margin-right: 1.5rem;
      color: $primary-color;
      font-size: 18px;
      line-height: 1;
      transition: 0.3s;

      svg {
        width: 17px;
        height: 16px;
        transform: translateY(3px);
        transition: 0.3s;
      }
    }

    &-arrow {
      transform: translateY(4px);
    }

    &:hover {
      .quick-links__link-item-text {
        color: $accent-color;

        svg {
          g {
            fill: $accent-color;
          }
        }
      }

      .quick-links__link-item-arrow {
        svg {
          path {
            stroke: $accent-color;
          }
        }
      }
    }
  }
}
