html {
  box-sizing: border-box;
}

body {
  color: $text-dark;
  line-height: 1.44;
  font-family: $font-body;
  font-size: 18px;
}

*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px, 1px, 1px, 1px);
  overflow: hidden;
  height: 1px;
  width: 1px;
  word-wrap: normal;
}

.overflow-hidden {
  overflow: hidden;
}
