.video-copy {
  &__top {
    padding-bottom: 4rem;

    @include small-medium {
      padding-bottom: 10rem;
    }

    @include medium {
      padding-bottom: 13rem;
    }
  }

  &__intro {
    max-width: 550px;
  }

  &__video-wrapper {
    width: 90%;
    max-width: 996px;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    margin-top: -2rem;

    @include small-medium {
      margin-top: -8rem;
    }

    @include medium {
      margin-top: -10rem;
    }
  }
}

// video component styling

.video-container {
  width: 100%;
  position: relative;
  overflow: hidden;
  height: 0;
  padding-top: 56.25%;

  .video-thumbnail {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 9 !important;
    pointer-events: none;
    -webkit-transform: translateZ(0);
    -webkit-backface-visibility: hidden;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  .button-video-play {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%) translateZ(0);
    z-index: 10 !important;
    border: none;
    background-color: rgb(252, 179, 21, 0.9);
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    transition: 0.3s;
    -webkit-transform: translateX(-50%) translateY(-50%) translateZ(0);
    -webkit-backface-visibility: hidden;
    box-shadow: 0 0 0 rgb(252, 179, 21, 0.9);
    animation: pulse 1.5s infinite;
    cursor: pointer;

    @include large {
      width: 79px;
      height: 79px;
    }

    &::after {
      content: '';
      display: block;
      width: 21px;
      height: 21px;
      background-image: url('../images/icons/triangle.svg');
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
      transform: translateX(1px);

      @include larger {
        width: 30px;
        height: 30px;
      }
    }
  }

  video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100% !important;
    height: 100% !important;
    object-fit: cover;
    z-index: 1 !important;
    padding: 0;
  }
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0px rgba(255, 196, 0, 0.9);
  }
  100% {
    box-shadow: 0 0 0 20px rgba(255, 196, 0, 0);
  }
}
