.team-listing {
  &__cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 1rem;
    row-gap: 1rem;

    @include small-medium {
      grid-template-columns: repeat(3, 1fr);
      column-gap: 1.5rem;
      row-gap: 1.5rem;
    }

    @include medium {
      grid-template-columns: repeat(4, 1fr);
    }

    @include xl {
      grid-template-columns: repeat(6, 1fr);
    }

    @include xl3 {
      column-gap: 3rem;
      row-gap: 3rem;
    }
  }

  &__show-more {
    display: none;

    &.visible {
      display: flex;
      justify-content: center;
      position: relative;
    }

    @include small-medium {
      display: none;
    }

    .overlay {
      width: 100%;
      height: 150px;
      background: rgb(255, 255, 255);
      background: linear-gradient(0deg, rgba(255, 255, 255, 0.9612219887955182) 0%, rgba(251, 251, 251, 0) 100%);
      position: absolute;
      top: -100px;
      z-index: 2;
      left: 0;
    }

    button {
      margin-top: 1rem;
      width: 3rem;
      height: 3rem;
      border: none;
      background-color: $primary-color;
      color: $white;
      border-radius: 50%;
      font-size: 2rem;
      line-height: 1;
      cursor: pointer;
      position: relative;
      z-index: 3;
    }
  }
}
