.image-swiper {
  .image-swiper-container {
    .swiper-slide {
      .image-copy {
        margin: 0 0 2.5rem 0;
      }
    }

    .swiper-button-next,
    .swiper-button-prev {
      top: 45%;
      width: 48px;
      height: 48px;
      border-radius: 50%;
      background-color: $secondary-color;
      transition: 0.3s;

      &::after {
        color: white;
        font-size: 1rem;
      }

      &:hover {
        filter: brightness(1.1);
      }
    }

    .swiper-button-disabled {
      background-color: white;

      &::after {
        color: #c3c6c8;
      }
    }

    .swiper-pagination {
      .swiper-pagination-bullet {
        &.swiper-pagination-bullet-active {
          background-color: $secondary-color;
        }
      }
    }
  }
}
