.card-person {
  &__link-wrapper {
    &:hover {
      img {
        filter: grayscale(0);
      }
    }
  }

  &__image {
    aspect-ratio: 1/1;
    margin-bottom: 8px;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      filter: grayscale(1);
      transition: 0.3s;
    }
  }

  &__name {
    color: $primary-color;
  }

  &__role {
    font-size: 14px;
  }
}
